import PlacesAutocomplete, { Suggestion } from "react-places-autocomplete";
import {Input, Form} from 'antd'
import React from 'react'

export const LocationAutocomplete: React.FC<{defaultValue: string, isModifyProfilePage: boolean}> = (props) => {
    return (
        <Form.Item
            label="Location"
            name="location"
            rules={[{ required: !props.isModifyProfilePage, message: !props.isModifyProfilePage ? `Location is required` : '' }]}
        >
            <PlacesAutocomplete>
                {({ getInputProps, getSuggestionItemProps, suggestions, loading }) => (
                    <>
                        <Input 
                            {...getInputProps({
                                id: "address-input"
                            })}
                            defaultValue={props.defaultValue} 
                            placeholder="New York City"/>
                        <div className="autocomplete-dropdown-container">
                                {loading ? <div>Loading...</div> : null}
                                {suggestions.map((suggestion: Suggestion) => {
                                    const className = suggestion.active ? "suggestion-item--active" : "suggestion-item";
                                    const style = suggestion.active
                                    ? { backgroundColor: "#fafafa", cursor: "pointer", padding: 25 }
                                    : { backgroundColor: "#ffffff", cursor: "pointer" };

                                    const spread: any = {
                                    ...getSuggestionItemProps(suggestion, {
                                        className,
                                        style
                                    })
                                    };

                                    return (
                                    <div {...spread} key={suggestion.id}>
                                        <div>{suggestion.description}</div>
                                    </div>
                                    );
                                })}
                        </div>  
                    </>
                )}         
            </PlacesAutocomplete>
        </Form.Item>
    )
}